.recipe {
  width: 40vw;
  display: grid;
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  background: white;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 50px, 1fr);
  grid-column-gap: 50px;
  grid-row-gap: 0px;
  margin-bottom: 100px;
}
.image {
  object-fit: contain;
  width: 100%;
  max-height: 400px;
}
ol {
  list-style-type: disc;
}
h1 {
  margin: 10px auto 10px auto;
  height: 50px;
}
.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
